      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border ref="table1" :data="TableData.tableData" :max-height="tableHeight" style="width: 100%" :summary-method="getSummaries" show-summary @selection-change="handleSelectionChange" v-loading="loading1">
          <el-table-column type="index" align="center" label="序号" width="45">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" width="150" label="销售组织">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" width="130" label="业务日期">
          </el-table-column>
