          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="not_delivery_num" width="100" label="未发货数量" align="right">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="delivery_goods_num" width="100" label="已发货数量" align="right">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_status_name" width="110" label="审核状态">
