          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="relevancy_sn" width="160" label="出库单号">
            <!-- 本期不上线 -->
            <template slot-scope="scope">
              <el-button @click="examine(scope,scope.row)" type="text" size="small">
                {{scope.row.relevancy_sn}}
