            <template slot-scope="scope">
              <span>{{scope.row.province_name+scope.row.city_name+scope.row.area_name}}</span>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="address" width="120" label="详细地址">
          </el-table-column>
