          </el-table-column>
        </el-table>
        <div class="table_sum" ref="table_sum">
          <ul style="overflow:auto;width:4000px;">
            <li style="width:1535px; text-align:left;padding-left:80px;">
              合计
            </li>
            <li style="width:100px;text-align:right">{{TableData.allList.total_not_delivery_num}}</li>
            <li style="width:100px;text-align:right">{{TableData.allList.total_delivery_num}}</li>
            <li style="width:110px;text-align:right"></li>
            <li style="width:160px;text-align:right"></li>
            <li style="width:160px;text-align:right"></li>
