</template>

<script>
import {
  getData,
  CompanySwitchClear,
  opthionList,
  tableResize //明细表去除页面滚动条
} from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
import getAreaList from "common/components/public/getAreaList";
export default {
  name: "instorageOtherDeliveryDetailOtherDeliveryDetailIndex",
  data() {
    return {
      defaultArea: [0], //默认的区域id
      outerVisible: false,
      TableData: {
        tableData: [],
        allList: {},
        total: 10
      },
      params: {
        auditor_status: 0,
        delivery_type: 0
      },
      tableHeight: 400
    };
  },
  components: {
    getAreaList //区域列表
  },
  computed: {
    ...mapGetters(["otherDeliveryDetailIndexGet"])
  },
  methods: {
    ...mapActions([
      "otherdeliveryinfoIndexACT",
      "otherDeliveryDetailSeeACT",
      "tirebackstorageSee",
      "threeoutstorageSeeACT",
      "auditreeoutExamine"
    ]),
    postageTypeListChange(val) {
      if (val != "") {
        this.params.postage_type_name = this.postageTypeList.filter(
          obj => obj.postage_type_id === val
        )[0].postage_type_name;
      }
    },
    deparmentChange(val) {
      if (val != "") {
        this.params.department_name = this.departmentList.filter(
          obj => obj.department_id === val
        )[0].department_name;
      }
    },
    logisticsListChange(val) {
      if (val != "") {
        this.params.logistics_name = this.logisticsList.filter(
          obj => obj.logistics_id === val
        )[0].logistics_name;
      }
    },
    getprovinceFocus() {
      this.$common.options("getAreaListOption", this.province, {
        level: 2
      });
    },
    changeDefaultArea(addressId) {
      this.params.cityList = addressId.join(",");
      this.params.province_id = addressId[0];
      this.params.city_id = addressId[1];
      this.params.area_id = addressId[2];
    },
    examine(scope, row) {
      if (row.delivery_type == "三包出库") {
        // 跳三包出库单
        this.threeoutstorageSeeACT({
          outstock_id: row.relevancy_id,
          goPage: this.goPage,
          params: this.params,
          pageSize: this.pageSize
        });
        this.$router.push({
          path: "/inventory/threeoutstorage/threeoutstorageSee"
        });
      } else if (row.delivery_type == "旧胎退回") {
        // 跳旧胎退回单
        this.tirebackstorageSee({
          outstock_id: row.relevancy_id,
          goPage: this.goPage,
          params: this.params,
          pageSize: this.pageSize
        });
        this.$router.push({
          path: "/inventory/tireback/tirebackSee"
        });
      } else {
        // 跳返胎出库单
        this.auditreeoutExamine({
          reborn_outstock_id: row.relevancy_id,
          params: this.params,
          goPage: this.goPage,
          pageSize: this.pageSize
        });
        this.$router.push({
          path: "/aftersale/treeoutree/auditreeout_examine"
        });
      }
    },
    examineGl(scope, row) {
      if (row.delivery_type == "三包出库") {
        // 跳三包订单
        this.otherDeliveryDetailSeeACT({
          other_delivery_id: row.other_delivery_id,
          params: this.params,
          goPage: this.goPage,
          pageSize: this.pageSize
        });
        this.$router.push({
          path: "/inventory/otherDeliveryDetail/otherdeliveryinfoSee"
        });
      } else if (row.delivery_type == "旧胎退回") {
        // 跳公司鉴定单
        this.otherDeliveryDetailSeeACT({
          other_delivery_id: row.other_delivery_id,
          params: this.params,
          goPage: this.goPage,
          pageSize: this.pageSize
        });
        this.$router.push({
          path: "/inventory/otherDeliveryDetail/otherdeliveryinfoSee"
        });
      } else {
        // 跳厂家预检单
        this.otherDeliveryDetailSeeACT({
          other_delivery_id: row.other_delivery_id,
          params: this.params,
          goPage: this.goPage,
          pageSize: this.pageSize
        });
        this.$router.push({
          path: "/inventory/otherDeliveryDetail/otherdeliveryinfoSee"
        });
      }
    },
    exportOrder(scheduleType) {
      let s = Object.assign(this.params, {
        export_type: scheduleType
      });
      this.$axMethod.exportOtherDeliveryInfo(this.params).then(res => {
        if (res.data.code == 200) {
          window.location.href = res.data.data.url;
        }
      });
    },
    companyChange(val) {
      if (!val) {
        this.$set(this, "departmentList", []);
        this.$set(this.params, "department_id", null);
        this.$set(this.params, "logistics_id", "");
      } else {
        this.params.company_name = this.companyList.filter(
          obj => obj.company_id === val
        )[0].company_name;
        CompanySwitchClear(this, this.params);
        this.$set(this.params, "logistics_id", "");
      }
    },
    getSummaries(param) {
      const { columns, data } = param;
      const sums = [];
      columns.forEach((column, index) => {
        if (index === 1) {
          sums[index] = "本页小计";
          return;
        }
        if (
          ["not_delivery_num", "delivery_goods_num"].includes(column.property)
        ) {
          const values = data.map(item => Number(item[column.property] || 0));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr);
          }, 0);
        }
        if (column.property === "postage_price") {
          const values = data.map(item => Number(item[column.property] || 0));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            return Number(prev + curr);
          }, 0);
          sums[index] = Number(sums[index])
            .toFixed(2)
            .toString()
            .replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1" + ",");
        }
      });
      return sums;
    },
    // handleSelectionChange(val) {
    //   this.multipleSelection = val;
    // },
    changeDefaultArea(addressId) {
      this.params.cityList = addressId.join(",");
      this.params.province_id = addressId[0];
      this.params.city_id = addressId[1];
      this.params.area_id = addressId[2];
    },
    onReset() {
      this.defaultArea = [0];
      this.$set(this, "params", {
        auditor_status: 0,
        delivery_type: 0
      });
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: this.params,
        TableData: this.TableData, //列表数据
        API: "getOtherDeliveryInfoList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams, 2);
    },
    onSearch() {
      if (this.timeValue) {
        this.params.business_start_time = this.timeValue[0];
        this.params.business_end_time = this.timeValue[1];
      } else {
        this.params.business_start_time = this.params.business_end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1
      });
      this.otherdeliveryinfoIndexACT({
        goPage: this.goPage,
        params: this.params,
        pageSize: this.pageSize
      });
      getData(this.searchParams, 2);
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1
      });
      getData(this.searchParams, 2);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val
      });
      getData(this.searchParams, 2);
    }
  },
  mounted() {
    const elTable1 = this.$refs.table1.$el.getElementsByClassName(
      "el-table__body-wrapper"
    )[0];
    const elTable2 = this.$refs.table_sum;
    elTable1.addEventListener("scroll", function() {
      elTable2.scrollLeft = this.scrollLeft;
    });
    if (
      this.otherDeliveryDetailIndexGet &&
      this.otherDeliveryDetailIndexGet.params
    ) {
      if (this.otherDeliveryDetailIndexGet.params.business_start_time) {
        const {
          business_start_time,
          business_end_time
        } = this.otherDeliveryDetailIndexGet.params;
        this.timeValue = this.timeValue.concat([
          business_start_time,
          business_end_time
        ]);
      }
      let objMessage = this.otherDeliveryDetailIndexGet.params;
      this.defaultArea = [
        objMessage.province_id,
        objMessage.city_id,
        objMessage.area_id
      ];
      this.$set(this, "params", this.otherDeliveryDetailIndexGet.params);
      opthionList(this, this.otherDeliveryDetailIndexGet.params);
    }

    this.$set(this, "searchParams", {
      params: {}, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getOtherDeliveryInfoList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams, 2, tableResize.bind(this));

    //取消单据右侧滚动条
    window.onresize = tableResize.bind(this);
  }
};
</script>
